import * as yup from 'yup'

const emailRegExp = /^[A-Za-z0-9._%+-]+@[A-Za-z0-9.-]+\.[A-Z|a-z]{2,}$/i
const passwordRegExp = /^[~`"!@#$%^&*()_+=[\]\{}|\\;':",.\/<>?a-zA-Z0-9-]+$/

const email = yup
  .string()
  .required('    ')
  .matches(emailRegExp, '   ')

const password = yup
  .string()
  .required('   ')
  .min(8, '     8 ')
  .matches(passwordRegExp, ' ')

export const loginSchema = yup.object().shape({
  email,
  password,
})
